<template>
<!-- 
  shopVpagePage    所有微页面链接的单选框
  vpageShow：boolen  是否展示
 -->
  <el-dialog :append-to-body="true" title="添加模板" :visible.sync="vpageShow" width="70%" :close-on-click-modal="false" :close-on-press-escape="false" :show-close="false">
    <!-- 筛选条件 -->
    <el-card class="filter-container" shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float: right" @click="getData" type="primary" size="small">查询</el-button>
        <el-button style="float: right;margin-right: 15px" @click="reset" size="small">重置</el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" :model="searchList" size="small" label-width="140px">
          <el-form-item label="模板名称:" prop="name">
          <el-input v-model="searchList.name" placeholder="请输入模板名称"></el-input>
        </el-form-item>
        </el-form>
      </div>
    </el-card>
    <el-card class="filter-container" shadow="never" style="margin-top:10px;">
      <el-table :data="list" style="width: 100%" border>
        <el-table-column prop="vpageName" label="页面名称"></el-table-column>
        <el-table-column prop="description" label="页面描述" show-overflow-tooltip>
          <template slot-scope="scope">{{scope.row.description ? scope.row.description : '-'}}</template>
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button @click="sub(scope.row)" type="text" size="small">选择</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div style="padding:10px 0 20px 0;">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :page-sizes="[5, 10, 15, 20]"
          :page-size="searchList.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="listTotal">
        </el-pagination>
      </div>
    </el-card>
    <span slot="footer" class="dialog-footer">
      <el-button @click="close">取 消</el-button>
    </span>
  </el-dialog>
</template>
<script>
import { mapState } from 'vuex'
import { shopVpagePage } from "@/api/shop/shopDecoration";
export default {
  props: ["vpageShow"],
  watch:{
    vpageShow(newvalue,oldValue){
      if(newvalue){
        this.getData();
      }
    },
  },
  data(){
    return{
      fromLoading:false,
      searchList:{
        pageNum:1,
        pageSize:5
      },
      list:[],
      listTotal:0,
    }
  },
  created(){
    this.searchList.shopId = this.shop.shopId
  },
  methods:{
    sub(e){
      console.log('e',e)
      this.$emit('vpageFun',[e])
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.searchList.pageSize = val;
      this.getData()
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.searchList.pageNum = val;
      this.getData()
    },
    reset(){
      this.searchList.productName = ''
      this.searchList.shopCategoryIdArr = []
      this.getData();
    },
    // 列表 - 获取数据
    getData(){
      this.fromLoading = true
      console.log('请求的数据',this.searchList)
      shopVpagePage(this.searchList).then(res=>{
        console.log('微页面数据列表',res)
        if(res.code == 200){
          this.list = res.data.list
          this.listTotal = res.data.total
        }
        this.fromLoading = false
      })
    },
    close(){
      this.$emit('vpageClose',true)
    },
    change(){
      this.$forceUpdate()
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>